<template>
    <div>
        <head-top webActive="home"></head-top>
        <div class="bgGray" id="orderConfirm">
            <div class="container">
                <el-breadcrumb separator-class="el-icon-arrow-right" class="mb10">
                    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>支付中心</el-breadcrumb-item>
                </el-breadcrumb>
                <div class="orderBg">
                    <h1>支付金额：<span>{{orderDetail.price_total}}</span>元</h1>
                    <div class="orderInfo">
<!--                        <div class="o_p" v-for="(item,index) in orderDetail.goods">-->
<!--                            <el-image :src="item.pic"></el-image>-->
<!--                            <div>-->
<!--                                <p>{{item.name}}</p>-->
<!--                                <p>{{item.spec_name}} 单价{{item.price}}</p>-->
<!--                            </div>-->
<!--                        </div>-->
                        <div class="o_p" v-for="(item,index) in orderDetail.goods" :key="index">
                            <div class="o_p_img">
                                <el-image class="opImg" :src="item.pic" fit="cover"></el-image>
                            </div>
                            <div class="o_p_info">
                                <h2>{{item.name}}</h2>
                                <p>规格：{{item.spec_name}}{{item.spec_unit}}</p>
                                <p>单价：￥{{item.price}}/{{item.supply_unit}}  |  数量：×{{item.number}}</p>
                            </div>
                        </div>
                    </div>
                    <div class="payBox flex f-nowrap j-bet">
                        <div class="payInbox ">
                            <div class="payPic wx">
                                <el-image class="payImg" :src="wxPay.wxImage" fit="fill"></el-image>
                            </div>
                            <div class="payTit">
                                <img src="../../images/wechat_logo.jpg" height="25" width="25"/>
                                <span>微信支付</span>
                            </div>
                        </div>
                        <div class="payInbox" @click="toAlipay">
                            <div class="payPic paypal">
                                <img class="mt30" src="../../images/paypal.png" height="96" width="75"/>
<!--                                <el-image class="payImg" src="../../images/paypal.png" fit=""></el-image>-->
                            </div>
                            <div class="payTit">
                                <img src="../../images/alipay_logo.jpg" height="25" width="25"/>
                                <span>支付宝</span>
                            </div>
                        </div>
                        <div class="payInbox">
                            <div class="payPic">
                                <p class="f16 fb pt30 ml15 mr15">对公转账</p>
                                <p class="f14 mt15 ml15 mr15">如需对公转账，获取汇款信息，请联系企业客服</p>
                                <el-button class="mt15" size="mini" round>
                                    <a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=2827578121&site=qq&menu=yes">
                                    <i class="el-icon-service mr5 f14"></i>点我联系
                                    </a>
                                </el-button>
                            </div>
                            <div class="payTit">
                                <span>对公转账</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {toWxpayOrder,toAlipayOrder,checkedWxOrderStatus,getPayOrdersData} from '../../service/api'
    import headTop from '../../components/header/headTop';

    export default {
        name: 'orderPay',
        data(){
            return{
                orderId:'',
                wxPay:{
                    wxImage:'',
                    orderNo:'',
                    money:''
                },
                timer: null,
                orderDetail:[],
                timeOut:0//订单支付超时
            }
        },
        components:{
            headTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),

            //初始化数据
            async initData(){
                this.orderId = this.$route.query.orderId||'';
                if (this.userInfo && this.userInfo.user_id){
                    //获取订单信息
                    var order = await getPayOrdersData(this.orderId);
                    this.orderDetail = order;


                    //获取微信支付二维码
                    var wx = await toWxpayOrder(this.orderId);
                    this.wxPay = wx;

                }else{
                    this.$message({
                        message: '请先登录',
                        type: 'warning'
                    });
                }
            },
            async toAlipay(){
                var res = await toAlipayOrder(this.orderId);
                window.open(res.alipayUrl,'_blank');
            },
            startLoop() {
                this.timer = setInterval(() => {
                    this.isPaySuccess()
                }, 3000);
            },
            async isPaySuccess() {
                var self = this;
                const orderId = self.orderId;
                var wxNumber = self.wxPay.orderNo ||'';
                if(wxNumber!=''){
                    const res = await checkedWxOrderStatus(wxNumber);
                    if (res.status === 12000) {
                        //支付成功
                        clearInterval(self.timer);
                        self.timer = null;
                        setTimeout(() => {
                            if(self.orderId==''){
                                self.$router.push({path:"/userCenter/order/order"});
                            }else{
                                self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                            }
                        }, 1500)
                    } else if(res.status === 18001){
                        //未知的错误发生
                        clearInterval(self.timer);
                        self.timer = null;
                        self.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                        setTimeout(() => {
                            if(self.orderId==''){
                                self.$router.push({path:"/userCenter/order/order"});
                            }else{
                                self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                            }
                        }, 1500)
                    }else{
                        //订单未支付
                        self.timeOut = self.timeOut +1;
                        if(self.timeOut == 20){
                            self.$message({
                                message: '订单支付已超时',
                                type: 'warning'
                            });
                            clearInterval(self.timer);
                            self.timer = null;
                            setTimeout(() => {
                                if(self.orderId==''){
                                    self.$router.push({path:"/userCenter/order/order"});
                                }else{
                                    self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.orderId}});
                                }
                            },1000);
                        }
                    }
                }
            },
        },
        created(){
            this.startLoop();
        },
        watch:{
            userInfo: function (value){
                this.initData();
            }
        }

    }

</script>

<style scoped>
    /*确认订单*/
    #orderConfirm{padding:10px 0;}
    .orderBg{background-color: #fff;padding:15px;}
    .orderBg h1{
        font-size:18px;
        margin-bottom: 15px;
        color:#333;
    }
    .orderBg h1 span{
        color: #d4282d;
        font-size: 22px;
        padding-right: 3px;
    }
    .orderInfo{
        margin-bottom:30px;
        background-color: #f2f2f2;
        padding:0 15px;
    }
    .payBox{
        padding:5px 200px;
    }
    .payInbox{
        text-align: center;
    }
    .payImg{
        width:200px;
        height: 200px;
        border-radius: 10px;
    }
    .payPic{
        width:200px;
        height: 200px;
        border-radius: 10px;
        border:1px solid #c89e59;
    }
    .payTit{
        line-height: 36px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        margin-top: 15px;
    }
    .payTit span{
        line-height: 24px;
        font-size:16px;
        padding-left: 5px;
    }
    .wx{
        border:1px solid #41b035;
    }
    .paypal{
        border:1px solid #009fe8;
    }
    .o_p{
        border-bottom:1px solid #e4e4e4;
        display: flex;
        flex-wrap: nowrap;
        padding:10px 0;
    }
    .o_p:last-child{
        border-bottom: none;
    }
    .o_p_img{
        width:60px;
        height: 60px;
        padding-right:10px
    }
    .opImg{
        width:60px;
        height: 60px;
        background-color: #fff;
    }
    .o_p_info h2{
        color:#333;
    }
    .o_p_info p{
        font-size: 12px;
        display: block;
        line-height: 18px;
    }


</style>



